import { faClose, faMinus, faPlus } from '@fortawesome/free-solid-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { useState } from 'react'

// That's Will Render The Cart Product Card 
export default function Product({ imageSrc }) {

    const [quntity, setQuntity] = useState(1);

    return (
        <div className='border rounded-[5px] p-5 mb-5 flex items-center justify-between flex-wrap'>

            <div className='w-full lg:w-[48%] relative flex items-center mb-5 py-3 border-b lg:border-none lg:mb-0'>

                <div className='px-2 border rounded-[5px]'>

                    <img src={require(`./images/image-${imageSrc}.png`)} className='object-contain' />

                </div>

                <div className='ms-3 flex-1'>

                    <h2 className='font-medium mb-1 text-[12px] sm:text-[16px]'>موبايل  آبل آيفون بشريحتين و 64 جيجا بالإضافة الى كاميرتين</h2>

                    <p className='gray-text'>50,00 رس</p>

                </div>


                <div className='w-[30px] h-[30px] rounded-full bg-[#F8F8F8] flex lg:!hidden items-center justify-center cursor-pointer'>

                    <FontAwesomeIcon icon={faClose} />

                </div>

            </div>

            <div className='w-full lg:w-fit flex items-center select-none'>


                <div className='flex items-center border rounded-[2px] mx-5 '>

                    <div className='w-[50px] h-[50px] flex items-center justify-center'>

                        <FontAwesomeIcon icon={faPlus} onClick={_ => setQuntity(quntity + 1)} className='cursor-pointer' />

                    </div>
                    <div className='w-[50px] h-[50px] border-r flex items-center justify-center'>

                        <p>{quntity}</p>

                    </div>
                    <div className='w-[50px] h-[50px] border-r flex items-center justify-center'>

                        <FontAwesomeIcon icon={faMinus} onClick={_ => quntity > 1 && setQuntity(quntity - 1)} className='cursor-pointer' />

                    </div>

                </div>

                <div className='w-[30px] h-[30px] rounded-full bg-[#F8F8F8] hidden lg:flex items-center justify-center cursor-pointer'>

                    <FontAwesomeIcon icon={faClose} />

                </div>

                <div className='ms-3 flex-1 flex justify-end lg:hidden'>


                    <h2 className='font-medium mb-1 text-[13px] sm:text-[16px]'>المجموع :50,000 ر.س</h2>

                </div>

            </div>

        </div>
    )
}
