import Product from "./components/Product";

// That's Will Render Cart Page
export default function Cart() {

    return (
        <section className='my-10'>

            <div className='container grid grid-cols-12 gap-5'>

                <div className='col-span-12 lg:col-span-8 xl:col-span-9'>

                    {
                        ['one', 'two', 'three'].map(item => {
                            return <Product key={item} imageSrc={item} />
                        })
                    }

                </div>

                <div className='col-span-12 lg:col-span-4 xl:col-span-3 border h-fit rounded-[5px] p-5'>

                    <div className="pb-5 border-b">

                        <h2 className="font-medium text-[20px]">ملخص الطلب</h2>

                        <div className="flex items-center justify-between my-4">

                            <p className="gray-text">مجموع المنتجات</p>
                            <p className="gray-text">145 رس</p>

                        </div>

                        <h6 className="font-medium text-[12px] mb-4">هل لديك كود خصم</h6>

                        <div className="flex items-center">

                            <input type="text" className="flex-1 p-3 border !border-l-0 rounded-[5px] text-[13px]" placeholder="أدخل كود الخصم" />

                            <button className="border border-[var(--min-color)] h-[45.5px] -ms-1 px-3 rounded-[5px] rounded-tr-none rounded-br-none text-[var(--min-color)]">إضافة</button>

                        </div>

                    </div>

                    <div className="mt-10">

                        <div className="flex items-center justify-between mb-3">

                            <h3 className="font-medium">الإجمالى</h3>
                            <h3 className="font-medium">145 رس</h3>

                        </div>

                        <p className="gray-text mb-3">الاسعار شاملة للضريبة</p>

                        <button className="min-btn w-full text-center mb-3">إتمام الطلبب</button>

                    </div>


                </div>

            </div>

        </section>
    )
}
